import { useForm, Controller } from "react-hook-form";
import { cn } from "../../../utils/custom_functions";
import moment from "moment";
import {
  DATETIME_ERROR_MESSAGE,
  DEFAULT_ADDITIONAL_INFORMATION,
  FLOORS_ARRAY,
  HELPERS_ARRAY,
} from "../../../utils/information";

import DatePicker from "react-datepicker";
import "../../../matols.css";
import { useCreateBookingLocalStorage } from "../../../utils/hooks/use_local_storage_createbooking";
import { LOCAL_STORAGE_KEYS } from "../../../utils/local_storage_keys";
import { useState } from "react";
import { ROUTE_LINKS } from "../../../utils/route_links";
import { Link } from "react-router-dom";
import { ErrorAlert } from "../alerts/alerts";
import { Button } from "../custom_button";

export function ChooseAdditionalInformationCard() {
  const [isDateTimeError, setIsDateTimeError] = useState(false);
  const { setCreateBookingObj, getCreateBookingObj } =
    useCreateBookingLocalStorage(LOCAL_STORAGE_KEYS.create_booking);

  const { register, handleSubmit, control } = useForm({
    defaultValues: DEFAULT_ADDITIONAL_INFORMATION,
  });

  // evaluate if date is correct
  const evaluateDateTime = (data) => {
    // convert to datatime objects
    const date = new Date(Date.parse(data["booking_date"]));
    const time = new Date(
      Date.parse(`${data["booking_date"]} ${data["booking_time"]}`)
    );

    // merge string
    const isError =
      new Date(
        Date.parse(
          `${date.getFullYear()}-${
            date.getMonth() + 1
          }-${date.getDate()} ${time.getHours()}:${time.getMinutes()}`
        )
      ) -
        Date.now() <
      5400000;

    setIsDateTimeError((r) => isError);

    return isError;
  };

  const onSubmit = (data) => {
    const bookingObj = getCreateBookingObj();

    console.log(data, "focus here");
    const isValidDateTime = evaluateDateTime(data);
    if (isValidDateTime) return;
    if (bookingObj) {
      setCreateBookingObj({ ...bookingObj, ...data });
      window.location.href = ROUTE_LINKS.makebooking;
    } else {
      setCreateBookingObj({ ...data });
      window.location.href = ROUTE_LINKS.makebooking;
    }
  };
  return (
    <div className="px-2">
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="custom_datepicker_width my-4">
          <Controller
            control={control}
            name="booking_date"
            render={({ field }) => (
              <DatePicker
                className={cn(
                  "text-sm rounded-lg text-gray-700 border-gray-200 focus:ring-yellow-400 focus:bg-white focus:border-yellow-400 py-3 px-4",
                  {
                    "border-red-500 bg-red-50 bg-opacity-100": isDateTimeError,
                  }
                )}
                dateFormat="yyyy-MMM-dd"
                timeFormat="HH:mm"
                minDate={Date.now()}
                showPopperArrow={false}
                closeOnScroll={true}
                selected={field.value}
                onChange={(date) =>
                  field.onChange(moment(date).format("yyyy-MM-DD"))
                }
                onFocus={(e) => (e.target.readOnly = true)}
              />
            )}
          />
          <span className="text-xs text-gray-400">Booking date</span>
        </div>
        <div className="custom_datepicker_width my-4">
          <Controller
            control={control}
            name="booking_time"
            render={({ field }) => (
              <DatePicker
                timeFormat="HH:mm"
                dateFormat="HH:mm"
                timeCaption="Time"
                className={cn(
                  "text-sm rounded-lg text-gray-700 border-gray-200 focus:ring-yellow-400 focus:bg-white focus:border-yellow-400 py-3 px-4",
                  {
                    "border-red-500 bg-red-50 bg-opacity-100": isDateTimeError,
                  }
                )}
                minDate={Date.now()}
                timeIntervals={15}
                showTimeSelect={true}
                showTimeSelectOnly={true}
                showPopperArrow={false}
                selected={new Date(Date.parse(`2024-06-05 ${field.value}`))}
                onChange={(time) =>
                  field.onChange(moment(time).format("HH:mm:ss"))
                }
                onFocus={(e) => (e.target.readOnly = true)}
              />
            )}
          />
          <span className="text-xs text-gray-400">Booking time</span>
        </div>
        <div>
          <select
            {...register("helpers", {
              valueAsNumber: true,
            })}
            id="helpers"
            name="helpers"
            className="block text-sm appearance-none w-full border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-lg leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400"
          >
            {HELPERS_ARRAY.map((_, i) => (
              <option key={i + 1} value={i + 1}>
                {i + 1} Helpers
              </option>
            ))}
          </select>
          <span className="text-xs text-gray-400">
            Request additional suport
          </span>
        </div>
        <div>
          <select
            {...register("floors", {
              valueAsNumber: true,
            })}
            id="floors"
            name="floors"
            className="mt-4 block text-sm appearance-none w-full  border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-lg leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400"
          >
            {FLOORS_ARRAY.map((_, i) => (
              <option key={i} value={i + 1}>
                {i + 1} Floors
              </option>
            ))}
          </select>
          <span className="text-xs text-gray-400">
            Total number of floors to carry up or down in both pick and dropoff
            locations
          </span>
        </div>
        <div>
          <select
            {...register("payment_option")}
            id="payment_option"
            name="payment_option"
            className="mt-4 block text-sm appearance-none w-full border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded-lg leading-tight focus:outline-none focus:ring-yellow-400 focus:bg-white focus:border-yellow-400"
          >
            {["EFT", "CASH"].map((paymentOption, i) => (
              <option key={i} value={paymentOption}>
                {paymentOption} payment
              </option>
            ))}
          </select>
          <span className="text-xs text-gray-400">
            Choose your prefared payment option
          </span>
        </div>

        <div className="mt-4">
          <textarea
            {...register("customer_driver_note")}
            type="text"
            rows={3}
            name="customer_driver_note"
            placeholder="Leave a note for the driver"
            className={cn(
              "text-sm text-gray-700 w-full rounded-lg border-gray-200 focus:bg-white focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none"
            )}
          />
          <span className="text-xs text-gray-400">
            Need to give the driver additional instruction?, leave a note for
            the driver here.
          </span>
        </div>
        {/*alert*/}

        {/* <button type="submit">Click me</button> */}
        <div className="py-4">
          {isDateTimeError && (
            <ErrorAlert
              className="mb-4"
              secondaryText={DATETIME_ERROR_MESSAGE}
            />
          )}
          <div className="flex flex-row items-center justify-end gap-3">
            {/*alert*/}
            <Link
              to={ROUTE_LINKS.chooseroutes}
              className="px-3 py-2 rounded-md text-yellow-400 bg-black font-bold border-none ring-0"
            >
              Back
            </Link>
            <Button className="w-auto">Select and continue</Button>
          </div>
        </div>
      </form>
    </div>
  );
}
